.kr--a-go-back {
  position: absolute;
  left: 30px;
  top: 30px;
  color: var(--primary-color);
  text-transform: uppercase;
  text-decoration: none;
  font-size: 16px;
  i {
    margin-right: 10px;
  }
  &:hover {
    text-decoration: underline;
    color: var(--primary-color);
  }
}