.forgot-password {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px;
  position: relative;
  header {
    margin-bottom: 30px;
    color: var(--primary-color);
    font-size: 20px;
    small {
      max-width: 300px;
      margin-right: auto;
      margin-left: auto;
      opacity: 0.75;
      text-align: center;
    }
  }
  form {
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    .form-field {
      margin-bottom: 15px;
      input, label {
        width: 208px;
        padding: 10px;
        text-align: center;
        background-color: var(--primary-color);
        color: var(--font-color);
        border: none;
        font-size: 16px;
        &::placeholder {
          color: var(--font-color);
        }
      }
    }
    .form-link {
      a {
        color: var(--primary-color);
        font-size: 16px;
      }
    }
    button[type="submit"] {
      width: 208px;
      padding: 7.5px 24px 7.5px 24px;
      background-color: var(--primary-color);
      color: var(--font-color);
      border-radius: 24px;
      border: 2px solid var(--primary-color);
      font-size: 16px;
      position: relative;
      span {
        position: absolute;
        left: 15px;
        top: 9px;
        i {
          &:last-child {
            transform: translateX(-4px);
          }
        }
      }
      &:before {
        content: "";
        width: 100%;
        height: 100%;
        border: 2px solid var(--font-color);
        border-radius: 24px;
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
      }
    }
  }
}

@media screen and (max-width: 575px) {
  .forgot-password {
    overflow-x: hidden;
    height: initial;
  }
}